import React from 'react';
import Link from 'gatsby-link';
import classNames from 'classNames';
import { connect } from 'react-redux';

import styles from '../../styles/sidebar.module.css';

const ProductLink = ({ to, children, showSidebar })=>
  <li style={{ fontSize: '20px', fontWeight: '300'}}>
    <Link to={to} onClick={() => { if(!showSidebar) showSidebar() }}>
      {children}
    </Link>
  </li>  

const mapStateToProps = ({ showSidebar }) => {
  return { showSidebar }
}
    
const mapDispatchToProps = dispatch => {
  return { showSidebar: () => dispatch({ type: 'SHOW_SIDEBAR' }) }
} 

const ConnectedProductLink = connect(mapStateToProps, mapDispatchToProps)(ProductLink)

const Sidebar = ({ showSidebar, links }) =>
  <aside 
    className={classNames({
      [`${styles.hidden}`]: !showSidebar, 
    })}
  >
    <ul>
    {
      links.map(link => (
        <ConnectedProductLink key={link.to} to={link.to}>
          {link.text}
        </ConnectedProductLink>
      ))
    }
    </ul>
  </aside>

const ConnectedSidebar = connect(mapStateToProps, {})(Sidebar)

export default ConnectedSidebar;



